import * as React from 'react';
import Board from './Board';
import * as Fn from './function';
import * as If from './interface';

/* ゲーム画面と指し手履歴を出力する「Gameコンポーネント」 */
class Game extends React.Component<any, If.IGameState> {

  /* コンストラクタ */
  public constructor(props: any) {
    super(props);
    this.state = {
      history: [{
        squares: Array<If.SquareType>(9).fill(null),
      }],
      stepNumber: 0,
      xIsNext: true,
    };
    // this.handleChange = this.handleChange.bind(this);
    // this.handleClick = this.handleClick.bind(this);
  }

  /* 四角枠のクリックイベント */
  public handleClick(i: number): void {
    const history: If.IHistoryData[] = this.state.history.slice(0, this.state.stepNumber + 1);
    const current: If.IHistoryData = history[history.length - 1];
    const squares: If.SquareType[] = current.squares.slice();

    if (Fn.calculateWinner(squares) || squares[i]) {
      // 勝者が確定している or 四角枠が埋まっている場合は何もしない
      return
    }

    // 四角枠を埋める
    squares[i] = this.state.xIsNext ? 'X' : 'O';
    this.setState({
      history: history.concat([{
        squares,
      }]),
      stepNumber: history.length,
      xIsNext: !this.state.xIsNext,
    });
  }

  /* 指し手履歴のクリックイベント */
  public jumpTo(step: number): void {
    this.setState({
      stepNumber: step,
      xIsNext: (step % 2) === 0,
    });
  }

  /* Gameコンポーネントをレンダリング */
  public render(): JSX.Element {
    const history: If.IHistoryData[] = this.state.history;
    const current: If.IHistoryData = history[this.state.stepNumber];
    const winnerSquare: If.SquareType = Fn.calculateWinner(current.squares);
    /* 指し手履歴の配列を返す */
    const moves: JSX.Element[] = history.map((historyData, index) => {
      const desc: string = index ?
        '指し手 #' + index + ' に戻る' :
        'ゲーム開始に戻る';
      const jumpTo: () => void = (
        () => this.jumpTo(index)
      );

      return (
        <li key={index} >
          <button
            onClick={jumpTo}
          > {desc}
          </button>
        </li>
      )
    });

    let status: string;
    if (winnerSquare) {
      status = '勝者: ' + winnerSquare;
    } else {
      status = '次のプレイヤー: ' + (this.state.xIsNext ? 'X' : 'O');
    }
    const handleClick: (i: number) => void = ((i) => this.handleClick(i));

    return (
      <div className="game">
        <div className="game-board">
          <Board
            squares={current.squares}
            onClick={handleClick}
          />
        </div>
        <div className="game-info">
          <div>{status}</div>
          <ol>{moves}</ol>
        </div>
      </div>
    )
  }
}

export default Game;



