import * as React from 'react';
import * as If from './interface';

/* ボード上の四角枠を出力する「Squareファンクション」 */
export function Square(props: If.ISquareProps): JSX.Element {
  return (
    <button
      className="square"
      onClick={props.onClick}
    >
      {props.square}
    </button>
  )
}

/* 勝敗を判定する「calculateWinnerファンクション」 */
export function calculateWinner(squares: If.SquareType[]): If.SquareType {

  const lines = [
    [0, 1, 2], // 横
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6], // 縦
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8], // 斜め
    [2, 4, 6],
  ];

  for (const row of lines) {
    const [a, b, c] = row;
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      // 勝者が確定
      return squares[a];
    }
  }

  // 勝者が未確定
  return null;
}



