import * as React from 'react';
import * as Fn from './function';
import * as If from './interface';

/* ボードを出力する「Boardコンポーネント」 */
class Board extends React.Component<If.IBoardProps, any> {

  /* ボード上の四角枠をレンダリング */
  public renderSquare(i: number): JSX.Element {
    const propsOnClick: () => void = (
      () => this.props.onClick(i)
    );

    return (
      <Fn.Square
        square={this.props.squares[i]}
        onClick={propsOnClick}
      />
    )
  }

  /* Boardコンポーネントをレンダリング */
  public render(): JSX.Element {
    return (
      <div>
        <div className="board-row">
          {this.renderSquare(0)}
          {this.renderSquare(1)}
          {this.renderSquare(2)}
        </div>
        <div className="board-row">
          {this.renderSquare(3)}
          {this.renderSquare(4)}
          {this.renderSquare(5)}
        </div>
        <div className="board-row">
          {this.renderSquare(6)}
          {this.renderSquare(7)}
          {this.renderSquare(8)}
        </div>
      </div>
    )
  }
}

export default Board;



